.about, .mission, .products, .founder, .home {

    padding: 20px;
    width: 85%;
    z-index: 10;
    /* position: absolute;
    top: 53%; */
}

.about:before {
  content: '';
  background: yellow;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: -1;
}
.mission:before {
  content: '';
  background:#000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: -1;
}
.products:before {
  content: '';
  background:#fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: -1;
}
.founder:before {
  content: '';
  background-color: #f9f2f3;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: -1;
}
.home {
  background-color: transparent !important;
  background: transparent !important;
}
.about p, .mission p, .products p, .founder p, .home p {
  font-size: 20px;
}
.founder img  {
  max-width: 100%;
}
.mission {
  margin-top: 150px;
  color: #fff;
}

.products {
  color: #000;
}
.video-container.mission-container {
  justify-content: flex-start;
}

.about.col-md-6.offset-md-3 p {
  font-size: 20px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.container {
  background-image: url(/chaskancha/static/media/background.e8bfd705.png);
    background-size: cover;
    background-repeat: no-repeat;
}
.font-video {
  width: 100%;
  height: 100%;
}
.video-container {
  display: flex;
  flex-direction: column;
  margin: 0!important;
  justify-content: center;
  align-items: center;

  height: 100vh;
}
 .home p, .home h1 {
  text-shadow: 1px 1px #fff;
}
video.videoTag {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
}
.footer {
  padding: 10px;
  background-color: lightblue;
  color: #000;
  text-shadow: 1px 1px #fff;
  bottom: 0;
  width: 100%;
  position: fixed;
  z-index: 999999999;
}
.App-link {
  color: #61dafb;
}
@media(max-width: 768px) {
.about, .mission, .products, .founder, .home {
      width: auto;

  }
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

